import { Homepagestyle, Itemswrapper ,Aboutlist} from "./style";
import React from "react";
import Header from "../../components/header/header";
import Item from "../../components/item/item";
import { Link } from "react-router-dom";
import flame from "../../assets/images/flames.png";
import itemimg from "../../assets/images/item.png";
// EXTINGUISHER IMAGES
import OP123 from "../../assets/images/extin-images/op01_02_03.jpg"
import OP456 from "../../assets/images/extin-images/op4_5_6.jpg"
import OP8_10 from "../../assets/images/extin-images/op8_10.jpg"
import OP25 from "../../assets/images/extin-images/op25.jpg"
import OP50 from "../../assets/images/extin-images/op50.jpg"
import OP100 from "../../assets/images/extin-images/op100.jpg"

function Homepage() {
  
  return (
    <Homepagestyle>
      <Header />
      <Aboutlist>
        <ul>
          Порошковые огнетушители
          <li>
            <img src={flame} alt="" />
            Огнетушители порошковые используются при тушении пожаров класса А, В
            (дерево, бумага, краски и ГСМ), С (Газообразные вещества) и
            электроустановок находящихся под напряжением до 1000B.
          </li>
          <li>
            <img src={flame} alt="" />
            Огнетушители бывают разных видов, и часто варьируются в зависимости
            от типа наполнителя. Порошковые огнетушители – это специальное
            устройство, в стальной корпус которого закачивается под давлением
            порошок.
          </li>
          <li>
            <img src={flame} alt="" />
            Открывание и закрывание аппарата происходит довольно – таки легко,
            благодаря специальному клапану, индикатору давления.
          </li>
          <li>
            <img src={flame} alt="" />
            Порошковые огнетушители снабжены манометром, который показывает
            давление в баллоне, тем самым сообщая пользователю об оставшейся
            работоспособности устройства.
          </li>
          <li>
            <img src={flame} alt="" />
            Эксплуатируются огнетушители при температуре от -40 до +50°С.
          </li>
          <li>
            <img src={flame} alt="" />
            Перезарядка один раз в 3 года.
          </li>
        </ul>
      </Aboutlist>
      <Itemswrapper>
        <h1>Популярные товары только у нас</h1>
        <section>
          <Item name="Огнетушитель Порошковый ОП-5" kg="5" img={(OP456)}  />
          <Item name="Огнетушитель Порошковый ОП-1" kg="1" img={(OP123)}  />
          <Item name="Огнетушитель Порошковый ОП-3" kg="3" img={(OP123)}  />
          <Item name="Огнетушитель Порошковый ОП-25" kg="25" img={(OP25)}  />
          <Item name="Огнетушитель Порошковый ОП-10" kg="10" img={(OP8_10)}  />
          <Item name="Огнетушитель Порошковый ОП-4" kg="4" img={(OP456)}  />
        </section>

        <button className="moreproducts">
          <Link to="/product">more products</Link>
        </button>
      </Itemswrapper>
    </Homepagestyle>
  );
}

export default Homepage;
