import styled from "styled-components";

export const Itemstyle = styled.div`
  width: 29vw;
  border: 1px solid #da112f;
  padding: 0.3vh 0.4vw;
  margin: 25px auto;
  margin-top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  transition-duration: 0.4s;
  overflow: hidden;
  @media (max-width: 767px) {
    width: 48vw;
  }
  @media (max-width: 512px) {
    width: 80vw;
  }
  .h3 {
    font-size: 2.2vw;
    font-family: Helvetica, sans-serif;
    padding: 0;
    margin: 0;
    @media (max-width: 788px) {
      font-size: 3vw;
    }
    @media (max-width: 512px) {
      font-size: 4vw;
    }
  }
  .h4 {
    font-size: 1.3vw;
    color: #da112f;
    @media (max-width: 788px) {
      font-size: 2.1vw;
    }
    @media (max-width: 512px) {
      font-size: 2.6vw;
    }
  }
  &:hover {
    background-color: #da112f;
    box-shadow: 0 0 5px rgb(235, 56, 42);
    color:white;
    .contact {
      color: white;
    }

    .contact {
      background-color: #da112f;
      border: 1px solid white;
    }

    .h4 {
      color: #ccc;
    }
  }
  img {
    width: 70%;
    margin: 20px auto 10px auto;
  }
  .contact {
    width: 10vw;
    height: 4.7vh;
    background-color: #fff;
    border: 1px solid #da112f;
    margin: 1.4vh auto;
    font-size: 1.3vw;
    letter-spacing: 1px;
    transition-duration: 0.4s;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (max-width: 788px) {
      font-size: 2.3vw;
      width: 17vw;
      height: 4.5vh;
    }
    @media (max-width: 512px) {
      font-size: 3.3vw;
      width: 24vw;
      height: 4.4vh;
    }
    color: #da112f;
    &:hover {
      background-color: #fff;
      color: #da112f;
      border: 1px solid white;
    }
  }
`;

export const Connection = styled.div`
  position: fixed;
  top: 0%;
  left:0%;
  background-color: rgba(0,0,0,0.1);
  backdrop-filter: blur(3px);
  padding:20vw;
  z-index: 222;
  cursor: pointer;
  transition-duration: 0.5s;
  transform: scale(${(props) => (props.isOpen ? "1" : "0")});
  @media(max-width: 788px){
    width: 100vw;
  }
  @media(max-width: 512px){
    width: 100vw;
  }
  
  .inside {
    width: auto:
    height: 35vh;
    background-color: white;
    border-top-left-radius: 20%; 
    border-top-right-radius: 0; 
    border-bottom-left-radius: 1.6vw; 
    border-bottom-right-radius: 50%;
    padding: 2.2vh 7vw 7vh 2.2vw;
    cursor: default;
    word-wrap: normal;
    position: relative;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    z-index:333;
    @media(max-width: 788px){
      margin-top: 50%;
      padding: 1.6vh 7.5vw 7.5vh 3vw;
    }
    @media(max-width: 512px){
      margin-top:100%;
      padding: 2vh 11.5vw 7.7vh 3.5vw;
    }
    button{
     width: auto;
     position:absolute;
     top:0;
     right:0;
     background-color: transparent;
     img{
      width: 2.5vw;
      @media(max-width: 788px){
        width: 3.4vw;
      }
      @media(max-width: 512px){
        width: 5vw;
      }
    }
    }
    h3{
      font-size: 2.3vw;
      @media(max-width: 788px){
        font-size: 3.3vw;
      }
      @media(max-width: 512px){
        font-size: 4.2vw;
      }
    }
    ol{
      list-style: none;
      padding: 0;
      margin:0;
      font-size: 1.7vw;
      @media(max-width: 788px){
       font-size: 2.5vw;
      }
      @media(max-width: 512px){
        font-size: 3.3vw;
      }
      img{
        width: 1.5vw;
        @media(max-width: 788px){
          width: 2.6vw;
        }
        @media(max-width: 512px){
          width: 3.5vw;
        }
      }
      li{
        margin-top: 15px;
        a{
          color: #DA112F;
          font-family: 'Anton', sans-serif;        }

      }
    }
  }
`;
