import React, { Component } from "react";
import { Connection, Itemstyle } from "./style";
import { Link } from "react-router-dom";
import Smart from "../../assets/images/smartphone.png";
import Telephone from "../../assets/images/telephone.png";
import Email from "../../assets/images/email.png";
import Close from "../../assets/images/free-icon-cancel-179429.png";
class item extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contactopen: false,
    };
  }
  render() {
    const contacthendler = (e) => {
      this.setState({
        contactopen: !this.state.contactopen,
      });
      console.log(this.props);
    };
    const contactClose = () => {
      this.setState((contactopen)=>this.state.contactopen = false);
    };

    return (
      <>
        <Itemstyle>
          <img src={this.props.img} alt="" />
          <h3 className="h3">{this.props.name}</h3>
          <h4 className="h4">класс ABCЕ / {this.props.kg} кг. с манометром</h4>
          <button onClick={contacthendler} className="contact">
            {/* <Link to="/contact">связаться</Link>
             */}
            связаться
          </button>
        </Itemstyle>
        <Connection isOpen={this.state.contactopen}>
          <div className="inside">
            <button>
              <img src={Close} onClick={contactClose} alt="X" />
            </button>
            <h3>Выберите как вы хотите связаться с нами?</h3>
            <ol>
              <li>
                <img src={Smart} alt="" /> Мобильный:{" "}
                <a href="tel: +998994897880">+998994897880</a>
              </li>
              <li>
                <img src={Telephone} alt="" /> Рабочий:{" "}
                <a href="tel: +998933897880">+998933897880</a>
              </li>
              <li>
                <img src={Email} alt="" /> Адрес электронной почты:{" "}
                <a href="/">angrenuyutservis@gmail.com</a>
              </li>
            </ol>
          </div>
        </Connection>
      </>
    );
  }
}

export default item;
